import { Component, OnInit, Output, EventEmitter } from '@angular/core';

import { MenuTagsService } from '../../service/menuTags/menu-tags.service';
import { AlertController } from '@ionic/angular';
import { ToastService } from '../../service/toast-service.service';
import { _HELPER } from "../../service/CAIDAT_HELPER";
@Component({
  selector: 'app-menu-tags',
  templateUrl: './menu-tags.component.html',
  styleUrls: ['./menu-tags.component.scss'],
})
export class MenuTagsComponent implements OnInit {
  dangTaoTags: Boolean = false
  tagsinput: string = ""
  tagsList = <any>[]
  filterBigList!: string[];

  @Output() filterByTag = new EventEmitter<string>()
  constructor(
    private menuTagsService: MenuTagsService,
    public alertController: AlertController,
    public toastController: ToastService,
  ) {

  }

  ngOnInit() {
    this.tagsList = this.menuTagsService.tagsList
    this.filterBigList = this.menuTagsService.filterBigList
  }
  async addTags() {
    if (this.tagsinput != "") {
      await this.menuTagsService.addTags(this.tagsinput)
      this.tagsinput = ""
    }
  }
  deleteTag(tag: string) {
    this.presentAlert(`Bạn thực sự muốn xóa tag <b>${tag}</b>?
    <br>- <b>Giữ trong hồ sơ</b>: khi thêm lại <b>${tag}</b>, hồ sơ sẽ tự có tag <b>${tag}</b>.
    <br>- <b>Xóa trong các hồ sơ</b>: tất cả <b>${tag}</b> sẽ bị xóa khỏi hệ thống. (Hồ sơ không mất, chỉ mất tag)`, tag)
  }
  deleteGiuHoSo(tag: string) {
    this.menuTagsService.deleteGiuHoSo(tag)
  }
  deleteXoaTagHoSo(tag: string) {
    this.menuTagsService.deleteXoaTagHoSo(tag)
  }
  async deleteTagsCombine(tag: string, target: any) {
    const alert = await this.alertController.create({
      header: 'Xóa tags:',
      message: `Xóa <b>${tag}</b>?`,
      buttons: [{
        text: 'Hủy',
        role: 'cancel'
      }, {
        text: 'Xóa',
        handler: () => {
          if (target.localName == "ion-icon") {
            // console.log(this.filterBigList)
            let ind = this.filterBigList.indexOf(tag)
            this.filterBigList.splice(ind, 1)
            this.menuTagsService.deleteCombineTags(this.filterBigList)
          }
        }
      }]
    });
    await alert.present();

  }
  helper(tag: string) {
    //@ts-ignore
    this.toastController.helper(_HELPER[tag])
  }
  filterTagClick(tag: string, target: any) {
    if (target.localName != "ion-icon") {
      this.filterByTag.emit(tag)
    }
  }
  async renameTagCheck(tag:string){
    console.log(tag)
    
    const alert = await this.alertController.create({
      header: 'Đổi tên tag!',
      inputs: [
        {
          name: 'newname',
          type: 'text',
          placeholder: 'Tên mới',
        }
      ],
      buttons: [
        {
          text: 'Hủy',
          role: 'cancel'
        }, {
          text: 'Ok',
          handler: (alertData) => {
            this.renameTag(tag, alertData['newname'])
          }
        }
      ]})
      alert.present()

  }

  renameTag(old: string, newname: string) {
    this.tagsList.delete(old)
    this.tagsList.add(newname)
    this.filterBigList = this.filterBigList.map((val, ind) => {
      console.log(val)
      let value = ""
      val.split(",").forEach((val1, ind1) => {
        if (val1 == old) {
          console.log(val1)
          value = value + (value==""? "": ",") + newname
        } else value = value + (value==""? "": ",") + val1
      })
      return value
    })
    this.menuTagsService.updateTagsList()
    this.menuTagsService.deleteCombineTags(this.filterBigList)
  }


  async presentAlert(msg: any, tag: any) {
    const alert = await this.alertController.create({
      header: 'Xóa tag',
      message: msg,
      buttons: [{
        text: 'Hủy',
        role: 'cancel'
      }, {
        text: 'Xóa nhưng giữ trong hồ sơ',
        handler: () => {
          this.deleteGiuHoSo(tag)
        }
      },
      {
        text: 'Xóa trong hồ sơ',
        cssClass: 'danger',
        handler: () => {
          this.deleteXoaTagHoSo(tag)
        }
      }]
    });
    await alert.present();
  }


}
